"use client";

import { useEffect, useRef } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import type { ProtocolStep } from "@/lib/types";

gsap.registerPlugin(ScrollTrigger);

const DEFAULT_STEP_IMAGES = [
  "https://images.unsplash.com/photo-1635070041078-e363dbe005cb?w=800&q=80",
  "https://images.unsplash.com/photo-1558591710-4b4a1ae0f04d?w=800&q=80",
  "https://images.unsplash.com/photo-1579546929518-9e396f3cc809?w=800&q=80",
];

function GeometricMotif() {
  const ref = useRef<SVGSVGElement>(null);
  useEffect(() => {
    if (!ref.current) return;
    const ctx = gsap.context(() => {
      gsap.to(ref.current, { rotation: 360, duration: 25, repeat: -1, ease: "none" });
    }, ref.current);
    return () => ctx.revert();
  }, []);
  return (
    <svg ref={ref} className="w-24 h-24 text-accent/50 shrink-0" viewBox="0 0 100 100" aria-hidden>
      <ellipse cx="50" cy="50" rx="45" ry="15" fill="none" stroke="currentColor" strokeWidth="1.5" />
      <ellipse cx="50" cy="50" rx="15" ry="45" fill="none" stroke="currentColor" strokeWidth="1.5" />
      <circle cx="50" cy="50" r="8" fill="currentColor" />
    </svg>
  );
}

function ScanlineGrid() {
  const lineRef = useRef<HTMLDivElement>(null);
  useEffect(() => {
    if (!lineRef.current) return;
    const ctx = gsap.context(() => {
      gsap.fromTo(
        lineRef.current,
        { x: "-100%" },
        { x: "100%", duration: 3, repeat: -1, ease: "none" }
      );
    }, lineRef.current);
    return () => ctx.revert();
  }, []);
  return (
    <div className="absolute inset-0 overflow-hidden rounded-[2rem] pointer-events-none">
      <div className="absolute inset-0 grid grid-cols-8 grid-rows-6 gap-px bg-primary/5">
        {Array.from({ length: 48 }).map((_, i) => (
          <div key={i} className="bg-accent/10 rounded-sm" />
        ))}
      </div>
      <div
        ref={lineRef}
        className="absolute top-1/2 left-0 w-1/2 h-px bg-accent/70 shadow-[0_0_12px_var(--color-accent)]"
        aria-hidden
      />
    </div>
  );
}

function WaveformEKG() {
  const pathRef = useRef<SVGPathElement>(null);
  useEffect(() => {
    if (!pathRef.current) return;
    const path = pathRef.current;
    const length = path.getTotalLength();
    path.style.strokeDasharray = String(length);
    const ctx = gsap.context(() => {
      gsap.fromTo(
        path,
        { strokeDashoffset: length },
        { strokeDashoffset: 0, duration: 2, repeat: -1, repeatDelay: 0.8, ease: "power2.inOut" }
      );
    }, path);
    return () => ctx.revert();
  }, []);
  return (
    <svg className="w-full h-20 text-accent/70 shrink-0" viewBox="0 0 200 60" aria-hidden>
      <path
        ref={pathRef}
        d="M0 30 Q20 10 40 30 T80 30 T120 30 T160 30 T200 30"
        fill="none"
        stroke="currentColor"
        strokeWidth="2"
        strokeLinecap="round"
      />
    </svg>
  );
}

const SCROLL_VH_PER_STEP = 180;

export function Protocol({ steps }: { steps: ProtocolStep[] }) {
  const containerRef = useRef<HTMLDivElement>(null);
  const cardsRef = useRef<(HTMLDivElement | null)[]>([]);
  const sorted = [...steps].sort((a, b) => a.order_index - b.order_index);
  const list = sorted.length >= 1 ? sorted : [
    { id: "1", step_number: 1, title: "Define", description: "Map intent to a structured experience model.", animation_type: "geometric" as const, image_url: DEFAULT_STEP_IMAGES[0], order_index: 0, is_published: true, created_at: "", updated_at: "" },
    { id: "2", step_number: 2, title: "Orchestrate", description: "Compose interactions with precise timing.", animation_type: "scanline" as const, image_url: DEFAULT_STEP_IMAGES[1], order_index: 1, is_published: true, created_at: "", updated_at: "" },
    { id: "3", step_number: 3, title: "Deliver", description: "Render and ship with cinematic fidelity.", animation_type: "waveform" as const, image_url: DEFAULT_STEP_IMAGES[2], order_index: 2, is_published: true, created_at: "", updated_at: "" },
  ] as ProtocolStep[];

  const totalVh = list.length * SCROLL_VH_PER_STEP;

  useEffect(() => {
    if (!containerRef.current) return;
    const cards = cardsRef.current.filter(Boolean) as HTMLDivElement[];
    const n = list.length;
    const ctx = gsap.context(() => {
      ScrollTrigger.create({
        trigger: containerRef.current,
        start: "top top",
        end: `+=${totalVh}vh`,
        pin: true,
        pinSpacing: true,
      });
      list.forEach((_, i) => {
        const card = cards[i];
        if (!card) return;
        const isFirst = i === 0;
        gsap.set(card, {
          scale: isFirst ? 1 : 0.92,
          filter: "none",
          opacity: isFirst ? 1 : 0.6,
          zIndex: isFirst ? n : i,
        });
      });
      ScrollTrigger.create({
        trigger: containerRef.current,
        start: "top top",
        end: `+=${totalVh}vh`,
        scrub: 1.2,
        onUpdate: (self) => {
          const p = self.progress;
          const segment = 1 / n;
          list.forEach((_, i) => {
            const card = cards[i];
            if (!card) return;
            const segStart = i * segment;
            const segEnd = (i + 1) * segment;
            let scale = 0.92;
            let opacity = 0.6;
            let zIndex = i;
            if (p >= segStart && p <= segEnd) {
              const t = (p - segStart) / segment;
              scale = 0.92 + 0.08 * t;
              opacity = 0.6 + 0.4 * t;
              zIndex = n;
            } else if (p > segEnd) {
              const t = Math.min(1, (p - segEnd) / segment);
              scale = 1 - 0.08 * t;
              opacity = 1 - 0.4 * t;
              zIndex = n;
            }
            gsap.set(card, { scale, opacity, zIndex });
          });
        },
      });
    }, containerRef);
    return () => ctx.revert();
  }, [list.length, totalVh]);

  return (
    <section id="protocol" className="relative bg-primary/95" aria-label="Protocol">
      <div
        className="relative"
        ref={containerRef}
        style={{ height: `${totalVh}vh` }}
      >
        <div className="sticky top-0 h-screen w-full flex items-center justify-center">
          {list.map((step, i) => (
            <div
              key={step.id}
              ref={(el) => { if (el) cardsRef.current[i] = el; }}
              className="absolute inset-4 sm:inset-6 flex items-center justify-center rounded-[3rem] overflow-hidden border border-ghost/10 mx-4 sm:mx-6"
              style={{
                zIndex: i,
                backgroundColor: "var(--color-background)",
              }}
            >
              <div className="flex flex-col sm:flex-row items-center justify-center gap-6 sm:gap-10 px-6 w-full max-w-4xl">
                <div className="flex flex-col items-center justify-center shrink-0 order-2 sm:order-1">
                  {step.animation_type === "geometric" && <GeometricMotif />}
                  {step.animation_type === "scanline" && (
                    <div className="relative w-40 h-24">
                      <ScanlineGrid />
                    </div>
                  )}
                  {step.animation_type === "waveform" && (
                    <div className="w-48">
                      <WaveformEKG />
                    </div>
                  )}
                  <span className="font-mono text-accent text-sm mt-3">
                    Step {step.step_number}
                  </span>
                  <h3 className="font-sans font-bold text-primary text-xl sm:text-2xl mt-1">
                    {step.title}
                  </h3>
                  {step.description && (
                    <p className="text-graphite/80 text-center text-sm max-w-xs mt-1">
                      {step.description}
                    </p>
                  )}
                </div>
                {(step.image_url || DEFAULT_STEP_IMAGES[i]) && (
                  <div className="relative w-full max-w-sm aspect-[4/3] rounded-2xl overflow-hidden bg-primary/5 shrink-0 order-1 sm:order-2">
                    <img
                      src={step.image_url || DEFAULT_STEP_IMAGES[i]}
                      alt=""
                      className="w-full h-full object-cover"
                    />
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
